<template>

    <div class="main">
        <form>
            <input v-model="email" placeholder="Email">
            <input type="password" v-model="password" placeholder="Пароль">
            <button @click="addUser()">Зарегистрироваться</button>
        </form>
        <form @submit="addData()">
          <h2>Add name and surname</h2>
          <input name="one" placeholder="Имя" class="input" required>
          <input name="two" placeholder="Фамилия" class="input" required>
          <button type='submit'>Добавить</button>
        </form>
        <button @click="deliteData()" type='submit'>Удалить</button>
        <button @click="findData()" type='submit'>Найти</button>
        <button @click="updateData()" type='submit'>Обновить</button>
    </div>
</template>

<script>
import 'firebase/auth'
import 'firebase/database'
import { db } from '../main'
export default {
    name: 'Registration',
    data() {
      return {
        email: '',
        password: '',
        name: '',
        surname: '',
      }
    },
    firestore () {
      return {
        comics: db.collection('comics').orderBy('createdAt')
      }
    },
    methods: {
      addData(){
        let form = document.forms[1]
        db.collection("users").add({
            name: form.elements.one.value,
            surname: form.elements.two.value
        })
      },
      findData(){
        db.collection("users").where("name", "==", "Владимир").get()
        .then(function(querySnapshot) {
            querySnapshot.forEach(function(doc) {
                let docId = doc.id
                //let docData = doc.data()
                return docId
            });
        })
      },
      deliteData(){
        let docId = this.findData()
        alert(docId)
        db.collection("users").doc(docId).delete()
      },
      updateData(){
        db.collection("users").doc("zujwIPpTGKaY3oqyM0V1")
      }
    }
  }


</script>

<style scoped>
.main{
    padding-top: 260px;
}
</style>